.section {
  & h2 {
    position: relative;
    margin: 0;
    padding: 0 0 0 115px;
    font-weight: 400;
    font-size: 16px;
    line-height: 20px;
    color: #06a9a3;

    &::before {
      counter-increment: section-counter;
      content: "0" counter(section-counter);
      position: absolute;
      left: 0;
      top: 0;
      font-weight: 300;
      font-size: 16px;
      line-height: 20px;
      color: #06a9b3;
    }

    &::after {
      content: "";
      position: absolute;
      width: 81px;
      height: 1px;
      left: 25px;
      top: 9px;
      background-color: #06a9b3;
    }
  }

  & h3 {
    margin: 60px 0 25px 0;
    font-size: 32px;
    line-height: 48px;
  }
}
